<template>
  <div>
    <sui-menu pointing secondary class="option-menu">
      <a
        is="sui-menu-item"
        v-for="item in options"
        :active="isActive(item.value)"
        :key="item.value"
        :content="item.text"
        @click="select(item.value)"
      />
    </sui-menu>
    <sui-dropdown fluid selection :options="options" v-model="current" class="option-dropdown" />

    <div class="ui segment">
      <div v-if="current === 'md5'">
        <MD5 />
      </div>
      <div v-else-if="current === 'sha1'">
        <SHA1 />
      </div>
      <div v-else-if="current === 'sha256'">
        <SHA256 />
      </div>
      <div v-else-if="current === 'sha512'">
        <SHA512 />
      </div>
      <div v-else-if="current === 'sha3'">
        <SHA3 />
      </div>
      <div v-else-if="current === 'ripemd160'">
        <RIPEMD160 />
      </div>
      <div v-else></div>
    </div>
  </div>
</template>

<script>
import MD5 from '@/components/MD5.vue';
import SHA1 from '@/components/SHA1.vue';
import SHA256 from '@/components/SHA256.vue';
import SHA512 from '@/components/SHA512.vue';
import SHA3 from '@/components/SHA3.vue';
import RIPEMD160 from '@/components/RIPEMD160.vue';

export default {
  name: 'CryptoHashing',
  components: {
    MD5,
    SHA1,
    SHA256,
    SHA512,
    SHA3,
    RIPEMD160,
  },
  data() {
    return {
      current: 'md5',
      options: [
        {
          text: 'MD5',
          value: 'md5',
        },
        {
          text: 'SHA1',
          value: 'sha1',
        },
        {
          text: 'SHA256',
          value: 'sha256',
        },
        {
          text: 'SHA512',
          value: 'sha512',
        },
        {
          text: 'SHA3',
          value: 'sha3',
        },
        {
          text: 'RIPEMD160',
          value: 'ripemd160',
        },
      ],
    };
  },
  methods: {
    isActive(name) {
      return this.current === name;
    },
    select(val) {
      this.current = val;
    },
  },
};
</script>
<style lang="scss">
.option-dropdown {
  display: none !important;
}

@media (max-width: 767.98px) {
  .option-menu {
    display: none !important;
  }
  .option-dropdown {
    display: block !important;
    margin-top: 0.3rem;
  }
}
</style>
