<template>
  <div class="container">
    <h2>Hashing - SHA3:= Keccak[c=2d]</h2>
    <p>
      SHA-3 is the winner of a five-year competition to select a new cryptographic hash algorithm
      where 64 competing designs were evaluated. Each of the SHA-3 functions is based on an instance
      of the Keccak algorithm, which NIST selected as the winner of the SHA-3 competition, but those
      SHA-3 functions won't produce hashes identical to Keccak.
    </p>
    <div class="ui card">
      <div class="content">
        <h4 class="ui sub header">Message String</h4>
        <div class="ui input">
          <textarea
            placeholder="Enter Message..."
            rows="6"
            spellcheck="false"
            v-model="input"
          ></textarea>
        </div>

        <h4 class="ui sub header">SHA3 Output</h4>
        <div class="ui input">
          <textarea placeholder="" rows="6" spellcheck="false" v-model="output"></textarea>
        </div>
      </div>

      <div class="extra content">
        <sui-dropdown placeholder="512" selection :options="options" v-model="outputLength" />

        <button class="ui primary button" @click="generate">
          Generate
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import CryptoJS from 'crypto-js';

export default {
  data() {
    return {
      input: '',
      output: '',
      outputLength: 512,
      options: [
        {
          text: '512',
          value: 512,
        },
        {
          text: '384',
          value: 384,
        },
        {
          text: '256',
          value: 256,
        },
        {
          text: '224',
          value: 224,
        },
      ],
    };
  },
  methods: {
    generate() {
      const hash = CryptoJS.SHA3(this.input, { outputLength: this.outputLength });
      this.output = `${hash}`;
    },
  },
};
</script>

<style scoped>
.ui.card {
  width: 100%;
}

.ui.input,
.ui.input textarea {
  width: 100%;
}

.ui.input textarea {
  resize: vertical;
  padding: 0.5rem;
}

button {
  margin-left: 1rem !important;
}
</style>
